import service from '../../service.js';
import mInput from '../../components/m-input.vue';
export default {
	components: {
		mInput
	},
	data() {
		return {
			phone: '',
			vercode: '',
			pwd: '',
			confirmpassword: '',
			reseturl:'/api/user/resetpwd',
			countdown: 60
		}
	},
	methods: {
		numberst(e) {
			//其他代码....
			this.countDown();
		},
		// 倒计时
		countDown() {
			let self = this;
			self.countdown = 60;
			self.countdown -= 1;
			if (self.clear) {
				clearInterval(self.clear)
			}
			self.clear = null;
			self.clear = setInterval(_ => {
				if (self.countdown > 0) {
					self.countdown -= 1;
				} else {
					clearInterval(self.clear)
				}
			}, 1000)
		},
		findPassword(reseturl) {
			/**
			 * 
			 */
			if (this.phone.length != 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号码格式错误'
				});
				return;
			}
			if (this.vercode.length != 6) {
				uni.showToast({
					icon: 'none',
					title: '验证码不正确'
				});
				return;
			}
			if (this.pwd.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.confirmpassword != this.password) {
				uni.showToast({
					icon: 'none',
					title: '两次密码不一致'
				});
				return;
			}
			const data = {
			        phone: this.phone,
			        pwd: this.pwd,
			        vercode: this.vercode,
					usertype:0
			    }
			reseturl= this.url + reseturl;
			uni.request({					
					url: reseturl,
					method:'POST',
					data:data,
					header: {
					    'Content-Type': 'application/x-www-form-urlencoded' //自定义请求头信息
					},
					success: (res) => {
						this.res =JSON.stringify(res);
						console.log(res.data)
						if(res.data.code===0){
							console.log("success")
							service.addUser(data);
							uni.showToast({
							    title: '重置密码成功'
							});
							uni.navigateTo({
								url:"/pages/index/index"
							})({
							    delta: 2
							});
						}else if(res.data.code===1){
							uni.showToast({
								content: res.msg,
								showCancel: false
							});
						}			
					},
					complete: () => {
						this.loading = false;
					}
			});
		}
	}
}
